<template>
    <div class="bigbox">
        <div class="top">
            <div class="goblack"><span class="back iconfont icon-fanhui" @click="back"></span></div>
        </div>
        <div class="likelist">
            <BorderTitle>收藏歌单</BorderTitle>
            <div class="songlist">
                <ul  v-if="likePlayList.length>0">
                    <Loading v-if="showLoading"></Loading>
                    <ListItem v-for="bb in likePlayList" :key="bb.id" :item="bb"></ListItem>
                </ul>
                <ul class="nosonglist" v-else>
                    暂无收藏歌单
                </ul>
            </div>

            <BorderTitle>收藏歌曲</BorderTitle>
            <div class="song">
                <ul  v-if="likeSongList.length>0">
                    <li v-for="item in likeSongList" :key="item.id">
                        <SongItem :item="item"></SongItem>
                    </li>
                    <Loading v-if="showLoading"></Loading>
                </ul>
                <ul class="nosong" v-else>
                    暂无收藏歌曲
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import BorderTitle from '@/components/BorderTitle'
import SongItem from '@/components/SongItem'
import Loading from '@/components/Loading'
import ListItem from '@/components/ListItem'
export default {
    name:'like',
    data(){
        return{
            His:false,//歷史記錄
            showLoading:true,
            likePlayList:[],//歌单
            likeSongList:[], //歌曲
        }
    },
    components:{
        BorderTitle,
        SongItem,
        Loading,
        ListItem
    },
    created(){
        let playArr = JSON.parse( window.localStorage.getItem('likePlayList'));
        let songArr = JSON.parse( window.localStorage.getItem('likeSongList'));
        //收藏歌单详情
        if(playArr){
            playArr.forEach( item => {
                this.$http.get('/playlist/detail?id=' + item).then(data=>{
                    // console.log('item1',item)
                    this.likePlayList.push(
                        {
                            "id" : item,
                            "name": data.data.playlist.name,
                            "playCount": data.data.playlist.playCount,
                            "picUrl" : data.data.playlist.coverImgUrl
                        }
                    );
                    this.showLoading = false;
                }).catch(err=>{
                    console.log(err);
                });
            });
        }
        //收藏歌曲详情
        if(songArr){
            this.loadMusic(songArr);
        }
    },
    methods: {
       loadMusic(songArr){
            //获取歌单详情
            // console.log(songArr);
            let ids = songArr.join(',');
            //获取歌曲详情
            if(ids.length>0){
                this.$http.get('/song/detail?ids='+ids).then( songData=>{
                    songData.data.songs.map(d=>{
                        d.song={
                            artists:d.ar
                            
                        }
                        this.likeSongList.push(d);
                        this.showLoading = false;
                        return d;
                    })
                }).catch(err=>{
                    console.log(err);
                });
            }
        },
        // 删除收藏歌单
        // deletePlayList(item){
        //     let playArr = JSON.parse( window.localStorage.getItem('likePlayList'));
        //     let index = playArr.indexOf(item);
        //     playArr.splice(index,1);
        //     window.localStorage.setItem('likePlayList',JSON.stringify(playArr));
        //     window.location.reload();
        // },
        //返回
        back(){
            this.$router.go(-1)
        }
    }
}
</script>

<style lang="less" scoped>
.bigbox{
    width: 100vw;
    height: 100vh;
    background-color: #FCFCFD;
    .top{
        display: flex;
        width: 100vw;
        height: 6vh;
        background-color: #d43c33;
        text-align: center;
        line-height: 34px;
        span{
            font-size: 26px;
            color: white;
        }
    }
    .likelist{
        width: 100vw;
        height: 90vh;
        .songlist{
            width: 100%;
            ul{
                display:flex;
                flex-wrap: wrap;
                padding-left: 10px;
                li{
                    width: 33%;
                    padding: 0px 2px;
                }  
            }
            .nosonglist{
                justify-content: center;
                color: #d43c33;
            }
        }
        .song{
            width: 100%;
            padding: 0px 10px;
            .nosong{
                text-align: center;
                color: #d43c33;
            }
        }    
    }
}
</style>